<template>
  <div class="warps">
    <my-el-header title="" />
    <div class="container">
      <div class="form-nav">
        <el-form :inline="true" :close-on-click-modal="false" :model="form" class="demo-form-inline" :rules="form"
                 ref="form">
          <el-form-item label="" prop="diaplanRateGroup">
            <el-input v-model="form.routerName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=" " prop="diaplanRateGroup">
            <el-input v-model="form.fifoId" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="diaplanRateGroup">
            <el-input v-model="form.desType" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="diaplanRateGroup">
            <el-input v-model="form.callType" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="find"></el-button>
            <el-button @click="clear"></el-button>
          </el-form-item>
        </el-form>
        <el-button type="primary" @click="showAddForm(null, '')"
        >
        </el-button
        >
      </div>
      <el-table
        height="calc(100vh - 100px - 100px - 100px - 100px)"
        :header-cell-style="{background:'#ccc', color: '#fff',}"
        style="width: 100%"
        :data="list">
        <el-table-column
          prop="date"
          label=""
          width="50">
          <template scope="scope">{{ scope.$index + 1 }}</template>
        </el-table-column>
        <el-table-column
          prop="routerName"
          label="">
        </el-table-column>
        <el-table-column
          prop="routerName"
          label="">
          <template scope="scope">
            <span v-if="scope.row.desType === 0"></span>
            <span v-else-if="scope.row.desType === 1"></span>
            <span v-else-if="scope.row.desType === 2">socket</span>
            <span v-if="scope.row.callType === 3"></span>
          </template>
        </el-table-column>
        <el-table-column
          prop="regular"
          label="">
        </el-table-column>
        <el-table-column
          prop="regularType"
          label="">
          <template scope="scope">
            <div v-if="scope.row.regularType === 1">
              
            </div>
            <div v-else>
              
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" :width="$store.state.tableMixWidth">
          <template scope="scope">
            <el-link
              style="margin-right: 20px"
              @click="showAddForm(scope.row, '')"
              type="info"
            >
            </el-link
            >
            <template>
              <el-popconfirm title="" @confirm="del(scope.row)">
                <el-link slot="reference">
                </el-link>
              </el-popconfirm>
            </template>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog :title="title" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
      <el-form :model="form">
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="addForm.routerName" autocomplete="off"></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="addForm.callType" placeholder="" style="width: 100%">
              <el-option :label="item.label" :value="item.value" v-for="item in callType"></el-option>
            </el-select>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="addForm.desType" placeholder="" style="width: 100%">
              <el-option :label="item.label" :value="item.value" v-for="item in desType"></el-option>
            </el-select>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="addForm.regular" autocomplete="off"></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="addForm.regularType" placeholder="" style="width: 100%">
              <el-option :label="item.label" :value="item.value" v-for="item in regularType"></el-option>
            </el-select>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="addForm.content" autocomplete="off"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>

    <my-footer />
  </div>
</template>

<script>
import myElHeader from "@/components/myElHeader";
import myFooter from "@/components/myFooter";
import { addRouter, delRouter, getRouter, upDataRouter } from "@/newwork/call-router";

export default {
  name: "callRouting",
  components: {
    myElHeader,
    myFooter
  },
  data() {
    return {
      form: {},
      addForm: {
        routerName: "", //
        regular: "", //
        regularType: "", // regularType
        desType: "", //0 1did2socket3: ivr
        callType: 0,// 0: 1:
        content: ""//content
      },
      list: [],
      dialogFormVisible: false,
      formLabelWidth: "120px",
      title: "",
      callType: [
        { label: "", value: 0 },
        { label: "", value: 1 }
      ],
      desType: [
        { label: "", value: 0 },
        { label: "", value: 1 },
        { label: "socket", value: 2 },
        { label: "ivr", value: 31 }
      ],
      regularType: [
        { label: "", value: 0 },
        { label: "", value: 1 }
      ]

    };
  },
  methods: {
    next() {
      this.form.pageNum++;
      this.getRouter(this.form);
    },
    prev() {
      this.form.pageNum--;
      this.getRouter(this.form);
    },
    change(e) {
      this.form.pageNum = e;
      this.getRouter(this.form);
    },
    find() {
      this.getRouter(this.form);
    },
    clear() {
      this.form = this.$options.data().form;
      this.getRouter(this.form);
    },
    showAddForm(row, title) {
      this.title = title;
      this.dialogFormVisible = true;
      if (title === "") {
        this.addForm = row;
      }
    },
    getRouter(form) {
      getRouter(form).then(res => {
        this.$bus.$emit("total", res.data.data.total);
        if (res.data.code === 200) {
          this.list = res.data.data.records;
          this.dialogFormVisible = false;
        }
      }).catch(e => {
        this.$message.error(e);
      });
    },
    submit(row) {
      if (this.title === "") {

        upDataRouter(this.addForm).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.getRouter(this.form);
            this.dialogFormVisible = false;
            this.$message.success("");
          } else {
            this.$message.error(res.data.msg);
          }
        }).catch(e => {
          console.log(e);

        });
      } else {
        addRouter(this.addForm).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.dialogFormVisible = false;
            this.getRouter(this.form);
            this.dialogFormVisible = false;
            this.$message.success("");
          } else {
            this.$message.error(res.data.msg);
          }
        }).catch(e => console.log(e));
      }
    },
    del(row) {
      delRouter(row.id).then(res => {
        if (res.data.code === 200) {
          this.getRouter(this.form);
          this.dialogFormVisible = false;
          this.$message.success("");
        } else {
          this.$message.error(res.data.msg);
        }
      });
    }
  },
  watch: {
    dialogFormVisible(val) {
      if (!val) {
        this.addForm = this.$options.data().addForm;
      }
    }
  },
  created() {
    this.getRouter(this.form);
  }
};
</script>

